import React, { Component } from "react";

export class Section3 extends Component {
  render() {
    return (
      <div className="section section3">
        <div className="section-title section-title-side">
          <div className="icon-numbering">
            <div className="line line1"></div>
            <h6 className="number">02</h6>
          </div>
          <h5>How it works?</h5>
        </div>
        <div className="list">
          <div className="list-item">
            <div className="list-item-logo"></div>
            <div className="list-item-text">
              <p>Green-blue deployment </p>
            </div>
          </div>
          <div className="list-item">
            <div className="list-item-logo"></div>
            <div className="list-item-text">
              <p>Separate environments for playing, testing, staging, demo, you name it!</p>
            </div>
          </div>
          <div className="list-item">
            <div className="list-item-logo"></div>
            <div className="list-item-text">
              <p>Prompt and easy roll-back to any previous version </p>
            </div>
          </div>
          <div className="list-item">
            <div className="list-item-logo"></div>
            <div className="list-item-text">
              <p>Fully transparent resource accounting via performance graphs and logs</p>
            </div>
          </div>
          <div className="list-item">
            <div className="list-item-logo"></div>
            <div className="list-item-text">
              <p>Zero downtime on application deployments and updates</p>
            </div>
          </div>
          <div className="list-item">
            <div className="list-item-logo"></div>
            <div className="list-item-text">
              <p>Change management that is easy to track and trace</p>
            </div>
          </div>
          <div className="list-item">
            <div className="list-item-logo"></div>
            <div className="list-item-text">
              <p>DevOps team support and expertise available 24x7x365 at your service</p>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Section3;
