import React, { Component } from "react";

export class Section5 extends Component {
  render() {
    return (
      <div className="section section5">
        <div className="section-title section-title-side">
          <div className="icon-numbering">
            <div className="line line1"></div>
            <h6 className="number">04</h6>
          </div>
          <h5>Baseline service components</h5>
        </div>
        <div className="list">
          <div className="list-item">
            <div className="list-item-logo list-item-logo1"></div>
            <div className="list-item-text">
              <h6>Kubernetes</h6>
              <p>Kubernetes for container orchestration</p>
            </div>
          </div>
          <div className="list-item">
            <div className="list-item-logo list-item-logo2"></div>
            <div className="list-item-text">
              <h6>Databases</h6>
              <p>Terraform for scaling and DR</p>
            </div>
          </div>
          <div className="list-item">
            <div className="list-item-logo list-item-logo3"></div>
            <div className="list-item-text">
              <h6>GitLab</h6>
              <p>GitLab for CI/CD pipeline with user and project management</p>
            </div>
          </div>
          <div className="list-item">
            <div className="list-item-logo list-item-logo4"></div>
            <div className="list-item-text">
              <h6>Kibana / Greylog</h6>
              <p>Kibana and Greylog for debugging and diagnostics</p>
            </div>
          </div>
          <div className="list-item">
            <div className="list-item-logo list-item-logo5"></div>
            <div className="list-item-text">
              <h6>Grafana</h6>
              <p>Grafana/Prometheus for monitoring and insights.</p>
            </div>
          </div>
          <div className="list-item">
            <div className="list-item-logo list-item-logo6"></div>
            <div className="list-item-text">
              <h6>Ansible</h6>
              <p>Ansible for configuration management </p>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Section5;
