import React, { Component } from "react";

export class Section6 extends Component {
  render() {
    return (
      <div className="section section6">
        <div className="section-title section-title-center">
          <div className="icon-numbering">
            <div className="line line1"></div>
            <h6 className="number">05</h6>
            <div className="line line2"></div>
          </div>
          <h5>Onboarding done in 4 easy steps</h5>
        </div>
        <div className="list">
          <div className="list-item">
            <div className="list-item-logo list-item-logo1">1</div>
            <div className="list-item-text">
              <h6>Infrastructure analysis</h6>
              <p>We analyse current setup and find a balanced approach to transition into our DevOps service.</p>
            </div>
          </div>
          <div className="list-item">
            <div className="list-item-logo list-item-logo2">2</div>
            <div className="list-item-text">
              <h6>Consulting and planning </h6>
              <p>We work together to setup and document the migration plan that involves infrastructure, people and processes to get you start taking advantage of our services.</p>
            </div>
          </div>
          <div className="list-item">
            <div className="list-item-logo list-item-logo3">3</div>
            <div className="list-item-text">
              <h6>The Migration </h6>
              <p>
                Done by us, supervised and tested by you. Once all the components such as CI/CD, load balancing, autoscaling, DR, backup and monitoring solutions are implemented we are ready for...
              </p>
            </div>
          </div>
          <div className="list-item">
            <div className="list-item-logo list-item-logo4">4</div>
            <div className="list-item-text">
              <h6> Going live</h6>
              <p>The final iteration of production data and code is deployed, and switchover is done. Welcome onboard and enjoy new and improved development, deployment and maintenance processes.</p>
            </div>
          </div>
          {/* <div className="list-item">
            <div className="list-item-logo list-item-logo5">5</div>
            <div className="list-item-text">
              <h6>Going live</h6>
              <p>You will enjoy benefits of evolutionary leap your development process just undertook, you can now afford to focus purely on code. Congratulations!</p>
            </div>
          </div> */}
        </div>
      </div>
    );
  }
}

export default Section6;
