import React, { Component } from "react";
import $ from "jquery";

export class Footer extends Component {
  constructor(props) {
    super(props);
    this.state = {};
    this.scrollToSection = this.scrollToSection.bind(this);
  }

  scrollToSection(element) {
    const projectsOffset = $("." + element).offset().top - 90;
    $("html,body").animate({ scrollTop: projectsOffset }, "slow");
  }

  render() {
    return (
      <div className="footer section">
        <div className="title">HyperOps</div>
        <div className="row">
          <div className="column">
            <div className="link" onClick={() => this.scrollToSection("section2")}>
              About
            </div>
            <div className="link" onClick={() => this.scrollToSection("section5")}>
              Tools
            </div>
            <div className="link" onClick={() => this.scrollToSection("section4")}>
              Benefits
            </div>
          </div>
          <div className="column">
            <div className="link" onClick={() => this.scrollToSection("section3")}>
              How it works
            </div>
            <div className="link" onClick={() => this.scrollToSection("section7")}>
              Demo
            </div>
            <div className="link" onClick={() => this.scrollToSection("footer")}>
              Contact us
            </div>
          </div>
          <div className="column">
            <div className="link">+370 678 03330</div>
            <div className="link"> devops@hyperops.net</div>
            <div className="link"></div>
          </div>
        </div>
        <div className="horizontal-line"></div>
        <div className="copyright">
          All right reserver <span>@HyperOps</span> - 2020
        </div>
      </div>
    );
  }
}

export default Footer;
